import clsx from 'clsx'
import React, { FC } from 'react'
import ReactRating from 'react-rating'
import { StarFillIcon } from '../../icons/StarFill'
import { RatingProps } from './types'
import './Rating.scss'

const Rating: FC<RatingProps> = ({
  fractions,
  value,
  isLoading,
  onChange,
  size,
  isReadonly,
  className,
}) => {
  const reactRatingProps = {
    readonly: isReadonly || isLoading,
    initialRating: value,
    fractions,
    onChange,
    className: clsx(className, 'RatingNext', size),
  }
  const symbolProps = {
    className: clsx({ RatingNext__LoadingStar: isLoading }),
    size,
  }
  return (
    <ReactRating
      {...reactRatingProps}
      emptySymbol={<StarFillIcon color='transparent' {...symbolProps} />}
      fullSymbol={<StarFillIcon {...symbolProps} />}
    />
  )
}

Rating.defaultProps = {
  value: 0,
  size: 'M',
  fractions: 2,
  isLoading: false,
  isReadonly: false,
}

export default Rating
